@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap);
*,*:before,*:after{box-sizing:border-box;margin:0;padding:0}body{font-family:'Montserrat', sans-serif}.content{width:100%;max-width:950px;margin:0 auto}.similar_products{margin-top:2rem}.similar_products h2{color:#542e90}.similar_products p{font-size:1.125rem;opacity:0.6;font-weight:500;letter-spacing:-0.25px}.scrollable{overflow-x:scroll;padding:0.5rem;padding-bottom:2rem}.scrollable::-webkit-scrollbar{height:4px}.scrollable::-webkit-scrollbar-track{background:#d9d9d9}.scrollable::-webkit-scrollbar-thumb{background:#542e90}.scrollable::-webkit-scrollbar-thumb:hover{background:#49287d}.cards_grid{display:flex;grid-gap:1.5rem;gap:1.5rem;margin-top:1.5rem}

*,*:before,*:after{box-sizing:border-box;margin:0;padding:0}body{font-family:'Montserrat', sans-serif}.content{width:100%;max-width:950px;margin:0 auto}.card{cursor:pointer;padding:0.5rem;border-radius:0.325rem;box-shadow:0 0 3px 1px rgba(0,0,0,0.2);min-width:215px;transition:-webkit-transform 0.3s ease-in-out;transition:transform 0.3s ease-in-out;transition:transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out}.card:hover{-webkit-transform:scale(1.05);transform:scale(1.05)}.card_image{position:relative}.card_image img{width:100%}.card_image .wrapper_360{width:2rem;height:2rem;position:absolute;top:0.5rem;left:0.5rem;background-color:white;border-radius:0.5rem;box-shadow:0 0 5px -2px rgba(0,0,0,0.5);padding:0.325rem}.card_info{padding:0.5rem}.card_info h4{font-size:0.75rem}.card_info .price_logo{padding:0.5rem 0;display:flex;align-items:center;justify-content:space-between}.card_info .price_logo .price strong{display:block;color:#542e90;margin-bottom:0.25rem}.card_info .price_logo .price .old_price{color:#aaa;text-decoration:line-through;font-size:0.75rem}.card_info .price_logo .price .discount{background-color:#fff100;padding:0.25rem 0.75rem;font-size:0.5rem;border-radius:3px;margin-left:0.5rem}.card_info .price_logo .brand_logo{width:45px}.card_info .rating{display:flex;align-items:center;justify-content:center;grid-gap:0.75rem;gap:0.75rem;padding:0.325rem 0}.card_info .rating .stars img{width:1rem}.card_info .rating strong{font-size:0.75rem}.card_info .pickup{font-size:0.625rem;text-align:center}

